import Bird from './bird';
import Pipe from './pipe';
import TheGround from './ground';
let bird = [];
let gameStatus = 'getReady';
let score = 0;
let ghelegh = true;
let performNow = 0;
let TOTAL = 120;
let savedBirds = [];
let counter = 1;
let slider;
let showBest = false;
let speed = 1;
export function setSpeed(s) {
    speed = s
}
export function setshowBest(ssb) {
    showBest = ssb
}
export default function sketch(p) {

    let canvas;
    let pipes = [];
    let img;
    let birdUp;
    let birdStraight;
    let birdDown;
    let background;
    let sky;
    let groundImg;
    let groundsArray = [];
    let pipeTopImg;
    let pipeBottomImg;
    let getReadyImg;
    let startButton;
    let gameOverImg;
    let scoreBoardImg;
    let reTryImg;
    p.preload = () => {
        img = p.loadImage('https://images-na.ssl-images-amazon.com/images/I/51TxyIoXrZL._SX425_.jpg');
        birdDown = p.loadImage(require('./assets/bird1.png'));
        birdStraight = p.loadImage(require('./assets/bird2.png'));
        birdUp = p.loadImage(require('./assets/bird3.png'));
        background = p.loadImage(require('./assets/flappybirdBackground.jpg'));
        sky = p.loadImage(require('./assets/flappybird_SkyBuilding.jpg'));
        groundImg = p.loadImage(require('./assets/flappyBridGrass.jpg'));
        pipeTopImg = p.loadImage(require('./assets/pipeGreen_top.png'));
        pipeBottomImg = p.loadImage(require('./assets/pipeGreen_bottom.png'));
        //font = p.loadFont(require('./myFont.ttf'));
        getReadyImg = p.loadImage(require('./assets/get-ready.png'));
        startButton = p.loadImage(require('./assets/start-button.png'));
        gameOverImg = p.loadImage(require('./assets/gameover.png'));
        scoreBoardImg = p.loadImage(require('./assets/scoreboard.png'));
        reTryImg = p.loadImage(require('./assets/retry-button.png'));




    }


    p.setup = (reSetup) => {
        canvas = p.createCanvas(360, 640);
        for (let i = 0; i < TOTAL; i++) {
            bird[i] = new Bird(p, birdDown, birdStraight, birdUp);
        }
        groundsArray.push(new TheGround(0), new TheGround(359));
        p.image(img, 0, 0, 50, 50);
    };


    p.draw = () => {

        for (let n = 0; n < speed; n++) {
            if (counter % 88 === 0) {
                pipes.push(new Pipe(pipeTopImg, pipeBottomImg));
                score++;
            }
            counter++;

            if (gameStatus !== 'getReady')
                for (let i = pipes.length - 1; i >= 0; i--) {
                    //pipes[i].show(p);
                    if (gameStatus === 'Playing') {
                        pipes[i].update(p);
                        for (let j = 0; j < bird.length; j++) {
                            if (pipes[i].hits(bird[j], score) || bird[j].offScreen(p)) {
                                //gameStatus = 'Defeat';
                                savedBirds.push(bird.splice(j, 1)[0]);
                            }
                        }

                    }


                    if (pipes[i].isoffscreen) {
                        pipes.slice(i, 1);
                    }
                }


            if (gameStatus === 'Playing') {
                for (let i = 0; i < bird.length; i++) {
                    bird[i].think(pipes, p.weight);
                    //bird[i].show(p, birdUp);
                    bird[i].update();
                }
            }

            if (bird.length === 0) {
                counter = 0;
                nextGeneration(p, birdDown, birdStraight, birdUp);
                pipes = [];
                score = 0;

            }


        }

        p.image(sky, 0, 0);
        if (gameStatus === 'getReady') {
            ghelegh = false;
            p.image(getReadyImg, 90, 100);
            p.image(startButton, 128, 400);
        } else if (gameStatus === 'Defeat') {
            p.image(scoreBoardImg, 68, 176);
            //p.textFont(font);
            p.textSize(24);
            p.fill(255);
            p.stroke('black');
            p.strokeWeight(3);
            p.text(score, 246, 230);
            p.image(gameOverImg, 90, 100);
            p.image(reTryImg, 128, 300);
        }



        if (gameStatus === 'Playing') {
            if (showBest) {
                for (let i = 0; i < bird.length; i++) {
                    bird[0].show(p, birdUp);
                    break;
                }
            } else {
                for (let i = 0; i < bird.length; i++) {
                    bird[i].show(p, birdUp);
                }
            }

        }

        if (gameStatus === 'Playing') {
            pipes.map(v => v.show(p))
        }

        for (let i = groundsArray.length - 1; i >= 0; i--) {
            groundsArray[i].show(p, groundImg);
            if (gameStatus === 'Playing' || gameStatus === 'getReady')
                groundsArray[i].update(p);
            if (groundsArray[i].x + 360 < 0) {
                groundsArray.splice(i, 1);
                groundsArray.push(new TheGround(350));


            }


        }

        if (gameStatus === 'Playing') {
            //p.textFont(font);
            p.textSize(80);
            p.fill(255);
            p.stroke('black');
            p.strokeWeight(4);
            p.text(score, 160, 100);
        }






    }





    p.keyPressed = () => {

    };
    p.mousePressed = () => {
        if (gameStatus === 'getReady' && p.mouseX > 128 && p.mouseX < 232 && p.mouseY > 400 && p.mouseY < 458) {
            groundsArray = [];
            pipes = [];
            p.setup('reSetup');
            gameStatus = 'Playing';
            for (let i = 0; i < bird.length; i++) {
                bird[i].up();
            }

            performNow = performance.now();

        }

        if (gameStatus === 'Playing') {
            // bird.up();


        }
        //bird[0].saveModel();

    }

    p.myCustomRedrawAccordingToNewPropsHandler = (newProps) => {
        if (canvas) //Make sure the canvas has been created
            p.fill(newProps.color);
    }
}



function nextGeneration(p, birdDown, birdStraight, birdUp) {
    console.log('next generation');

    //setTimeout(()=>p.noLoop(),1000)
    calculateFitness();
    for (let i = 0; i < TOTAL; i++) {
        bird[i] = pickOne(p, birdDown, birdStraight, birdUp);
    }
    savedBirds = [];
}

function pickOne(p, birdDown, birdStraight, birdUp) {
    let index = 0;
    let r = Math.random(1);
    while (r > 0) {
        r = r - savedBirds[index++].fitness;

    }

    let nbird = savedBirds[index - 1];

    let child = new Bird(p, birdDown, birdStraight, birdUp, nbird.brain);
    child.mutate(0.08);
    return child;
}

function pickBest(p, birdDown, birdStraight, birdUp) {


    let bstScore = 0;
    let bstBrd;
    for (let brdy of savedBirds) {
        if (brdy.fitness > bstScore) {
            bstScore = brdy.fitness;
            bstBrd = brdy;
        }
    }
    let child = new Bird(p, birdDown, birdStraight, birdUp, bstBrd.brain);
    child.mutate(0.15);
    return child;
}



function calculateFitness() {
    let sum = 0;
    for (let bird of savedBirds) {
        sum += bird.score;
    }
    for (let bird of savedBirds) {
        bird.fitness = bird.score / sum;
    }
}